/*
 * Smooth scrolling
 */
(function () {

	"use strict";

	function handleClick (event) {


		const hash = event.currentTarget.hash;
		const element = document.querySelector(hash);

		if (element) {
			setTimeout(function(){
				element.scrollIntoView({ behavior: "smooth", block: "start" });
			}, 10);
		} else {
			throw new Error(`Element not found for hash: ${hash}`);
		}
		event.preventDefault();
	}

	const elements = Array.from(document.querySelectorAll("a[href^='#']"));

	elements.forEach(function (element) {
		element.addEventListener("click", handleClick);
	});

}());

// handle app badge slider
(function ($) {

	var swiper_settings = {
		slidesPerView: 10,
		watchSlidesProgress: true,
		watchSlidesVisibility: true,
		a11y: true,
		grabCursor: true,
		spaceBetween: 35,
		breakpoints: {
			1280: {
				slidesPerView: 8
			},
			1024: {
				slidesPerView: 6
			},
			990: {
				slidesPerView: 5
			},
			480: {
				slidesPerView: 4
			},
			340: {
				slidesPerView: 4
			}
		}
	};

	var navswiper = undefined;

	$(document).ready(function () {
		var pathname = window.location.pathname;
		$("a.q-apps-nav__link[href=\""+ pathname +"\"]").parents(".q-apps-nav__item").addClass("q-apps-nav__item--active");

		$(window).on("resize", function () {

			// function to hide or show slider nav buttons where applicable
			function hideShowNavButtons() {
				var $prevButton = $(".apps-nav-button--prev");
				var $nextButton = $(".apps-nav-button--next");

				if (navswiper.isBeginning) {
					$prevButton.addClass("hidden");
				} else {
					$prevButton.removeClass("hidden");
				}

				if (navswiper.isEnd) {
					$nextButton.addClass("hidden");
				} else {
					$nextButton.removeClass("hidden");
				}
			}

			var screenWidth = $(window).width();

			if(screenWidth >= 720 && navswiper === undefined) {
				navswiper = new Swiper(".swiper-container-nav", swiper_settings);

				setTimeout(function(){
					// get index value from hash, if it exists
					var hashIndex = window.location.hash.replace("#/index/", "");

					// if index value doesn't exist in URL hash
					if (hashIndex === "") {
						// slide to index of active badge
						navswiper.slideTo($(".q-apps-nav__item--active").index(), 0, false);
					} else {
						// otherwise, slide to index in URL hash
						navswiper.slideTo(hashIndex, 0, false);
					}

					hideShowNavButtons();

					$(".q-apps-mask").addClass("hide");
					setTimeout(function(){
						$(".q-apps-mask").remove();
					}, 500);
				}, 0);

				// when user clicks left nav button, ..
				$(".apps-nav-button--prev").on("click", function () {
					// slide to left by 3 slides
					navswiper.slideTo(navswiper.activeIndex - 3, 500);
					hideShowNavButtons();
				});

				// when user clicks left nav button, ...
				$(".apps-nav-button--next").on("click", function () {
					// slide to right by 3 slides
					navswiper.slideTo(navswiper.activeIndex + 3, 500);
					hideShowNavButtons();
				});

				// when user clicks a badge link, ...
				$(".swiper-slide a").on("click", function (event) {
					event.preventDefault();
					// get index of first visible badge
					var firstVisibleBadgeIndex = $(".swiper-slide-visible").index();
					var href = $(event.currentTarget).prop("href");
					// redirect to original URL + hash containing first visible badge index
					window.location.href = href + "#/index/" + firstVisibleBadgeIndex;
				});
			} else if (screenWidth < 720 && navswiper !== undefined) {
				navswiper.destroy(true,true);
				navswiper = undefined;
			}
		}).resize();


		// pie swiper
		var gdprswiper = new Swiper(".swiper-container-gdpr", {
			pagination: {
				el: ".swiper-pagination",
				clickable: true,
			},
			autoplay: {
				delay: 6000,
				disableOnInteraction: true,
				pauseOnMouseEnter: true,
			},
			navigation: {
				nextEl: ".swiper-button-next",
				prevEl: ".swiper-button-prev",
			},
		});
		gdprswiper.on("slideChange", function() {
			$(".q-grdp__pie-svg").attr("class","q-grdp__pie-svg q-grdp__pie-svg--active-"+gdprswiper.activeIndex);
		});

		gdprswiper.init();

		$(".q-gdrp__pie, .q-gdrp__pie-text").each(function(){
			var slice = $(this),
				slide = slice.data("slide");

			slice.on("click", function(){
				gdprswiper.slideTo(slide);
			});
		});


	});
}(jQuery));
